.book_item {
  box-sizing: border-box;
  opacity: 0;
  background-color: var(--light-background);
  border: 5px solid var(--violet);
  transform-origin: 0 100%;
  width: 40px;
  height: 120px;
  list-style: none;
  animation: 2.5s linear infinite travel;
  position: absolute;
  top: -120px;
  transform: translateX(300px);
}

.book_item.first {
  height: 140px;
  top: -140px;
}

.book_item.first:before, .book_item.first:after {
  content: "";
  background-color: var(--violet);
  width: 100%;
  height: 5px;
  position: absolute;
  top: 10px;
  left: 0;
}

.book_item.first:after {
  top: initial;
  bottom: 10px;
}

.book_item.second:before, .book_item.second:after, .book_item.fifth:before, .book_item.fifth:after {
  box-sizing: border-box;
  content: "";
  border-top: 5px solid var(--violet);
  border-bottom: 5px solid var(--violet);
  width: 100%;
  height: 17.5px;
  position: absolute;
  top: 10px;
  left: 0;
}

.book_item.second:after, .book_item.fifth:after {
  top: initial;
  bottom: 10px;
}

.book_item.third:before, .book_item.third:after {
  box-sizing: border-box;
  content: "";
  border: 5px solid var(--violet);
  border-radius: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 10px;
  left: 9px;
}

.book_item.third:after {
  top: initial;
  bottom: 10px;
}

.book_item.fourth {
  height: 130px;
  top: -130px;
}

.book_item.fourth:before {
  box-sizing: border-box;
  content: "";
  border-top: 5px solid var(--violet);
  border-bottom: 5px solid var(--violet);
  width: 100%;
  height: 17.5px;
  position: absolute;
  top: 46px;
  left: 0;
}

.book_item.fifth {
  height: 100px;
  top: -100px;
}

.book_item.sixth {
  height: 140px;
  top: -140px;
}

.book_item.sixth:before {
  box-sizing: border-box;
  content: "";
  background-color: var(--violet);
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: 31px;
  left: 0;
}

.book_item.sixth:after {
  box-sizing: border-box;
  content: "";
  border: 5px solid var(--violet);
  border-radius: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  bottom: 10px;
  left: 9px;
}

.book_item:nth-child(2) {
  animation-delay: .416667s;
}

.book_item:nth-child(3) {
  animation-delay: .833333s;
}

.book_item:nth-child(4) {
  animation-delay: 1.25s;
}

.book_item:nth-child(5) {
  animation-delay: 1.66667s;
}

.book_item:nth-child(6) {
  animation-delay: 2.08333s;
}

.shelf {
  background-color: var(--violet);
  width: 300px;
  height: 5px;
  margin: 0 auto;
  position: relative;
}

.shelf:before, .shelf:after {
  content: "";
  background: var(--light-background);
  background-image: radial-gradient(var(--violet) 30%, transparent 0);
  background-position: 0 -2.5px;
  background-size: 10px 10px;
  width: 100%;
  height: 100%;
  animation: .25s linear infinite move;
  position: absolute;
  top: 200%;
  left: 5%;
}

.shelf:after {
  top: 400%;
  left: 7.5%;
}

@keyframes move {
  from {
    background-position-x: 0;
  }

  to {
    background-position-x: 10px;
  }
}

@keyframes travel {
  0% {
    opacity: 0;
    transform: translateX(300px)rotateZ(0)scaleY(1);
  }

  6.5% {
    transform: translateX(279.5px)rotateZ(0)scaleY(1.1);
  }

  8.8% {
    transform: translateX(273.6px)rotateZ(0)scaleY(1);
  }

  10% {
    opacity: 1;
    transform: translateX(270px)rotateZ(0);
  }

  17.6% {
    transform: translateX(247.2px)rotateZ(-30deg);
  }

  45% {
    transform: translateX(165px)rotateZ(-30deg);
  }

  49.5% {
    transform: translateX(151.5px)rotateZ(-45deg);
  }

  61.5% {
    transform: translateX(115.5px)rotateZ(-45deg);
  }

  67% {
    transform: translateX(99px)rotateZ(-60deg);
  }

  76% {
    transform: translateX(72px)rotateZ(-60deg);
  }

  83.5% {
    opacity: 1;
    transform: translateX(49.5px)rotateZ(-90deg);
  }

  90% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: translateX(0)rotateZ(-90deg);
  }
}

.animate-bottom {
